/* =========================================================================
   Custom Styling
   ========================================================================= */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

html *,
html *:before,
html *:after {
  box-sizing: inherit;
}

body {
  font-family: "Arial", "Helvetica Neue", Helvetica, serif;
  font-size: 1.6em;
  line-height: 1.4;
}

a {
  color: #EF4C23;
  text-decoration: none;
}

a:hover {
  color: #EF4C23;
  text-decoration: underline;
}

/* Hide visually but not from screen readers */
.hide {
  position: absolute !important;
  left: -9999em !important;
}

/* Image Replacement */
.ir {
  display: block;
  position: relative;
  overflow: hidden;
}

.ir span {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

/* Acessibility navigation */
.accessibility-nav {
  position: absolute;
  top: 0;
  left: -9999em;
  z-index: 1000;
}

.accessibility-nav a {
  position: absolute;
  top: 0;
  white-space: nowrap;
}

.accessibility-nav a:active,
.accessibility-nav a:focus {
  left: 9999em;
}

/* Container */
.container {
  position: relative;
  padding: 0 20px;
  margin: 0 auto;
  background-color: #E8E3D6;
  min-height: 100vh;
}

/* Header */
.site-name {
  width: 200px;
  height: 50px;
}

.site-name span {
  background: url(../img/common/logo.png) no-repeat;
}

/* Navigation */
/* Content */
#content {
  color: #7a6b45;
}

#content .news-wrapper {
  margin: 1.5em 0;
}

#content .news-wrapper .column {
  width: 50%;
}

#content .news-wrapper .column.left {
  float: left;
  border-right: dotted 1px;
}

#content .news-wrapper .column.left .header {
  -webkit-border-radius: 10px 0 0 0;
  -moz-border-radius: 10px 0 0 0;
  -ms-border-radius: 10px 0 0 0;
  border-radius: 10px 0 0 0;
}

#content .news-wrapper .column.right {
  float: right;
  border-right: solid 1px transparent;
}

#content .news-wrapper .column.right .header {
  -webkit-border-radius: 0 10px 0 0;
  -moz-border-radius: 0 10px 0 0;
  -ms-border-radius: 0 10px 0 0;
  border-radius: 0 10px 0 0;
}

#content .news-wrapper .column .header {
  background-color: #E50052;
  color: #fff;
  padding: 1em;
  text-transform: uppercase;
}

#content .news-wrapper .column .news {
  background-color: #fff;
  padding: 1em;
}

#content .news-wrapper .column .news h5 {
  color: black;
}

#content .news-wrapper .column .news a.gallery img {
  max-width: 100%;
}

#content .news-wrapper .column .news .description .more {
  color: #E50052;
  margin-top: 1em;
}

#content .news-wrapper .column .news .description .more a {
  color: #E50052;
}

#content .news-wrapper .column .news .description .more:after {
  content: "\f045";
  font-family: "FontAwesome";
  display: inline-block;
  margin-left: 1em;
}

#content .news-wrapper .column .news div:first-of-type {
  padding: 0;
}

#content .news-wrapper .column .news div:last-of-type {
  padding-right: 0;
}

/* Sidebar */
.sidebar {
  color: #7a6b45;
}

.sidebar .box {
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  border-radius: 10px;
  -webkit-box-shadow: 2px 2px 5px 0px #7a6b45;
  box-shadow: 2px 2px 5px 0px #7a6b45;
  background-color: #fff;
  margin: 1.5em 0;
}

.sidebar .box .header {
  border-bottom: dotted 1px;
  padding: 0.7em 0 1em 1em;
}

.sidebar .box .header h4 {
  color: #E50052;
  text-transform: uppercase;
  font-weight: bold;
}

.sidebar .box .content {
  padding: 0.7em;
  margin: 0.8em 0;
}

.sidebar .box .content p {
  margin: 0 0 4px;
}

.sidebar .box .content .social-icons {
  display: flex;
  justify-content: space-between;
  padding-bottom: 1em;
}

.sidebar .box .content .social-icons span.fa {
  vertical-align: middle;
}

.sidebar .box .content .social-icons span.fa.fa-facebook-square {
  color: #3b5998;
}

.sidebar .box .content .social-icons span.fa.fa-twitter-square {
  color: #6CADDE;
}

.sidebar .box .content .social-icons span.fa.fa-google-plus-square {
  color: #7a6b45;
}

.sidebar .box .content ul.ico {
  list-style: none;
  padding-left: 0;
}

.sidebar .box .content ul.ico li {
  padding-bottom: 1em;
}

.sidebar .box .content ul.ico li:before {
  content: "\f045";
  font-family: "FontAwesome";
  display: inline-block;
  margin-right: 1em;
}

/* Footer */
.footer {
  clear: both;
}

/*
    Colorbox Core Style:
    The following CSS is consistent between example themes and should not be altered.
*/
#colorbox, #cboxOverlay, #cboxWrapper {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  overflow: hidden;
}

#cboxWrapper {
  max-width: none;
}

#cboxOverlay {
  position: fixed;
  width: 100%;
  height: 100%;
}

#cboxMiddleLeft, #cboxBottomLeft {
  clear: left;
}

#cboxContent {
  position: relative;
}

#cboxLoadedContent {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

#cboxTitle {
  margin: 0;
}

#cboxLoadingOverlay, #cboxLoadingGraphic {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#cboxPrevious, #cboxNext, #cboxClose, #cboxSlideshow {
  cursor: pointer;
}

.cboxPhoto {
  float: left;
  margin: auto;
  border: 0;
  display: block;
  max-width: none;
  -ms-interpolation-mode: bicubic;
}

.cboxIframe {
  width: 100%;
  height: 100%;
  display: block;
  border: 0;
  padding: 0;
  margin: 0;
}

#colorbox, #cboxContent, #cboxLoadedContent {
  box-sizing: content-box;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
}

/* 
    User Style:
    Change the following styles to modify the appearance of Colorbox.  They are
    ordered & tabbed in a way that represents the nesting of the generated HTML.
*/
#cboxOverlay {
  background: url(../img/colorbox/overlay.png) repeat 0 0;
  opacity: 0.9;
  filter: alpha(opacity=90);
}

#colorbox {
  outline: 0;
}

#cboxTopLeft {
  width: 21px;
  height: 21px;
  background: url(../img/colorbox/controls.png) no-repeat -101px 0;
}

#cboxTopRight {
  width: 21px;
  height: 21px;
  background: url(../img/colorbox/controls.png) no-repeat -130px 0;
}

#cboxBottomLeft {
  width: 21px;
  height: 21px;
  background: url(../img/colorbox/controls.png) no-repeat -101px -29px;
}

#cboxBottomRight {
  width: 21px;
  height: 21px;
  background: url(../img/colorbox/controls.png) no-repeat -130px -29px;
}

#cboxMiddleLeft {
  width: 21px;
  background: url(../img/colorbox/controls.png) left top repeat-y;
}

#cboxMiddleRight {
  width: 21px;
  background: url(../img/colorbox/controls.png) right top repeat-y;
}

#cboxTopCenter {
  height: 21px;
  background: url(../img/colorbox/border.png) 0 0 repeat-x;
}

#cboxBottomCenter {
  height: 21px;
  background: url(../img/colorbox/border.png) 0 -29px repeat-x;
}

#cboxContent {
  background: #fff;
  overflow: hidden;
}

.cboxIframe {
  background: #fff;
}

#cboxError {
  padding: 50px;
  border: 1px solid #ccc;
}

#cboxLoadedContent {
  margin-bottom: 28px;
}

#cboxTitle {
  position: absolute;
  bottom: 4px;
  left: 0;
  text-align: center;
  width: 100%;
  color: #949494;
}

#cboxCurrent {
  position: absolute;
  bottom: 4px;
  left: 58px;
  color: #949494;
}

#cboxLoadingOverlay {
  background: url(../img/colorbox/loading_background.png) no-repeat center center;
}

#cboxLoadingGraphic {
  background: url(../img/colorbox/loading.gif) no-repeat center center;
}

/* these elements are buttons, and may need to have additional styles reset to avoid unwanted base styles */
#cboxPrevious, #cboxNext, #cboxSlideshow, #cboxClose {
  border: 0;
  padding: 0;
  margin: 0;
  overflow: visible;
  width: auto;
  background: none;
}

/* avoid outlines on :active (mouseclick), but preserve outlines on :focus (tabbed navigating) */
#cboxPrevious:active, #cboxNext:active, #cboxSlideshow:active, #cboxClose:active {
  outline: 0;
}

#cboxSlideshow {
  position: absolute;
  bottom: 4px;
  right: 30px;
  color: #0092ef;
}

#cboxPrevious {
  position: absolute;
  bottom: 0;
  left: 0;
  background: url(../img/colorbox/controls.png) no-repeat -75px 0;
  width: 25px;
  height: 25px;
  text-indent: -9999px;
}

#cboxPrevious:hover {
  background-position: -75px -25px;
}

#cboxNext {
  position: absolute;
  bottom: 0;
  left: 27px;
  background: url(../img/colorbox/controls.png) no-repeat -50px 0;
  width: 25px;
  height: 25px;
  text-indent: -9999px;
}

#cboxNext:hover {
  background-position: -50px -25px;
}

#cboxClose {
  position: absolute;
  bottom: 0;
  right: 0;
  background: url(../img/colorbox/controls.png) no-repeat -25px 0;
  width: 25px;
  height: 25px;
  text-indent: -9999px;
}

#cboxClose:hover {
  background-position: -25px -25px;
}

/*
  The following fixes a problem where IE7 and IE8 replace a PNG's alpha transparency with a black fill
  when an alpha filter (opacity change) is set on the element or ancestor element.  This style is not applied to or needed in IE9.
  See: http://jacklmoore.com/notes/ie-transparency-problems/
*/
.cboxIE #cboxTopLeft,
.cboxIE #cboxTopCenter,
.cboxIE #cboxTopRight,
.cboxIE #cboxBottomLeft,
.cboxIE #cboxBottomCenter,
.cboxIE #cboxBottomRight,
.cboxIE #cboxMiddleLeft,
.cboxIE #cboxMiddleRight {
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#00FFFFFF,endColorstr=#00FFFFFF);
}

